import "./navbar.scss";
import LogoLight from "../../icon/logol.png";
import LogoDark from "../../icon/logod.png";
import { Link, useNavigate } from "react-router-dom";
import { useContext } from "react";
import { DarkModeContext } from "../../context/darkmodeContext";
import Sun from "../../icon/sun.png";
import Moon from "../../icon/dark.png";
import { AuthContext } from "../../context/authContext";

export default function Navbar() {
  const { darkMode, toggle } = useContext(DarkModeContext);
  const { currentUser, logout } = useContext(AuthContext);
  const navigate = useNavigate();

  const handleLogout = (e) => {
    e.preventDefault();
    logout();

    navigate("/");
  };
  return (
    <div className="navbar">
      <div className="contanier">
        <div className="logo">
          <Link to={"/"}>
            {darkMode ? (
              <img src={LogoDark} alt="" />
            ) : (
              <img src={LogoLight} alt="" />
            )}
          </Link>
        </div>
        <div className="mode" onClick={toggle}>
          {darkMode ? <img src={Sun} alt="" /> : <img src={Moon} alt="" />}
        </div>
        {currentUser && (
          <div className="user">
            <img
              src={
                currentUser.img ||
                "data:image/png;base64,"
              }
              alt=""
            />
          </div>
        )}
        <div className="links">
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to={"/?cat=art"}
          >
            <h6>ART</h6>
          </Link>
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to={"/?cat=science"}
          >
            <h6>SCIENCE</h6>
          </Link>
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to={"/?cat=technology"}
          >
            <h6>TECHNOLOGY</h6>
          </Link>
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to={"/?cat=cinema"}
          >
            <h6>CINEMA</h6>
          </Link>
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to={"/?cat=design"}
          >
            <h6>DESIGN</h6>
          </Link>
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to={"/?cat=food"}
          >
            <h6>FOOD</h6>
          </Link>
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to={"/?cat=sport"}
          >
            <h6>SPORT</h6>
          </Link>
          <Link
            style={{ textDecoration: "none", color: "inherit" }}
            to={"/?cat=politics"}
          >
            <h6>POLITICS</h6>
          </Link>

          {currentUser ? (
            <>
              <span>{currentUser.username}</span>
              <span onClick={handleLogout}>Logout</span>
            </>
          ) : (
            <Link
              style={{ textDecoration: "none", color: "inherit" }}
              to={"/login"}
            >
              <span>Login</span>
            </Link>
          )}
          <span className="write">
            <Link
              to={"/write"}
              style={{ textDecoration: "none", color: "inherit" }}
            >
              Write
            </Link>
          </span>
        </div>
      </div>
    </div>
  );
}
